import React, {useState,useEffect} from 'react';
import { GrLinkedin } from "@react-icons/all-files/gr/GrLinkedin";
import { MdEmail } from "react-icons/md";
import { IoLocation } from "react-icons/io5";
import { theme } from '../../theme.js'

const Footer = () => {


   const [isMobile, setIsMobile] = useState(false);

    useEffect(() => {
      const handleResize = () => {
        setIsMobile(window.innerWidth <= 768);
      };
      handleResize();
      window.addEventListener('resize', handleResize);

      return () => {
        window.removeEventListener('resize', handleResize);
      };
    }, []);

  const footerStyle = {
    display: 'flex',
    justifyContent: 'space-between',
    backgroundColor: '#ffffff',
    margin:isMobile ? '0px':'30px',
  };

  const leftSectionStyle = {
     display: 'flex',
     flexDirection:'column',
  };

  const rightSectionStyle = {
    display: 'flex',
    flexDirection:'column',
    justifyContent: 'space-between',
  };

  const siteNameViewStyle = {
    display: 'flex',
    flexDirection: 'row',
  };

  const sitetitleStyle = {
    marginLeft: '8px',
    fontFamily:theme.fonts.primary,
    fontSize: isMobile ? '16px':'24px',
    fontWeight:'700',
    marginTop:'0px'
  };

  const textStyle = {
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    margin:0,
    marginLeft:'8px',
    marginTop:isMobile ? '-2px': '0px',
    fontSize: isMobile ? '0.85em':'1em' 
  };

  const lineStyle = {
    backgroundColor: '#D3D3D3',
    height: '1px',
    width: '100%',
    marginBottom:'10px'
  };


  const logoSize = isMobile ? 17:25

  return (
    <footer>
      <div style={lineStyle}></div>
      <div style={footerStyle}>
        <div style={leftSectionStyle}>
          <div style={siteNameViewStyle}>
            <p style={{...sitetitleStyle, fontFamily:'Ubuntu'}}>\../</p>
            <p style={sitetitleStyle}>Stacqtrace</p>
            <a href="https://www.linkedin.com/company/stacqtrace" target="_blank" rel="noopener noreferrer">
              {/*<GrLinkedin size={logoSize} style={{marginLeft:'8px',color:theme.colors.primary}}/>*/}
            </a>
          </div>
          <p style={{...textStyle, marginLeft:'8px'}}>{theme.footer.text}</p>
        </div>
        <div style={rightSectionStyle}>
          <div style={siteNameViewStyle}>
            <MdEmail size={logoSize}/>
            <a href={`mailto:contact@stacqtrace.com`} style={{ ...textStyle, marginBottom:'16px ', cursor: 'pointer', textDecoration: 'underline' }}>
              contact@stacqtrace.com
            </a>
          </div>
          <div style={siteNameViewStyle}>
            <IoLocation size={logoSize}/>
            <div>
              <p style={textStyle}>Delhi, India</p>
              <p style={textStyle}>Sydney, Australia</p>
              <p style={textStyle}>Auckland, New Zealand</p>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
